import axios from 'axios'
import { Message, MessageBox } from 'element-ui'
import store from '../store'
import { getToken } from '@/utils/auth'

// axios
const service = axios.create({
  baseURL: process.env.BASE_API, // api  base_url
  timeout: 10000 // 
})

// request
service.interceptors.request.use(
  config => {
    console.log('do...')
    if (config.params !== undefined) {
      for (var k in config.params) {
        if (config.params[k] === '' || config.params[k] === undefined || config.params[k] === null) {
          delete config.params[k]
        }
      }
    }
    if (store.getters.token) {
      config.headers['mg_auth'] = getToken() // token 
      console.log('config', config)
    }
    return config
  },
  error => {
    // Do something with request error
    console.log(error) // for debug
    Promise.reject(error)
  }
)

// response 
service.interceptors.response.use(
  response => {
    console.log(response)
    const res = response.data
    if (res.result !== 100) {
      Message({
        message: res.message,
        type: 'error',
        duration: 5 * 1000
      })

      // 50008:token; 50012:;  50014:Token ;
      if (res.result === 50008 || res.result === 50012 || res.result === 50014) {
        MessageBox.confirm(
          '',
          '',
          {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }
        ).then(() => {
          store.dispatch('FedLogOut').then(() => {
            location.reload() // vue-router bug
          })
        })
      }
      return Promise.reject('error')
    } else {
      return res.data
    }
  },
  error => {
    console.log(error) // for debug
    Message({
      message: error.message,
      type: 'error',
      duration: 5 * 1000
    })
    return Promise.reject(error)
  }
)

export default service



